import { DataServiceProvider } from './../../providers/data-service/data-service';
import { Component } from '@angular/core';
import { HomePage } from '../home/home';
import { NavController, NavParams } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl, FormControl } from '@angular/forms';
import { regexValidators } from './../validator';
import { PasswordValidation } from '../signup/password-validation';
import { CategoryPage } from '../category/category';

//@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {

  countries:any;
  signUpForm: FormGroup;

  constructor(private dataService:DataServiceProvider, private formBuilder: FormBuilder, public navCtrl: NavController, public navParams: NavParams) {
    this.signUpForm = this.formBuilder.group({
      firstName: new FormControl('Himanshu',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.name)
      ])
    ),
      lastName: new FormControl('Sharma',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.name)
      ])
    ),
      mobile: new FormControl('1234567890',Validators.compose([
        Validators.required,
        Validators.minLength(10),
        Validators.maxLength(15),
        Validators.pattern(regexValidators.phone)
      ])
    ),
      postCode: new FormControl('123456',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.postalCode)
      ])
    ),
      country: new FormControl('United States',Validators.required),

      location: new FormControl('Florida',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.location),
        Validators.minLength(3),
        Validators.maxLength(20)
      ])
    ),
      address: new FormControl('123, Melbourne street',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.address),
        Validators.minLength(20),
        Validators.maxLength(150)
      ])
    ),
      email: new FormControl('abc@gmail.com',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.email)
      ])
    ),
      password: new FormControl('ost@2018',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.password)
      ])
    ),
      confirmPass: new FormControl('ost@2018',Validators.compose([
        this.confirmPass,
        Validators.required,
        Validators.pattern(regexValidators.password)
      ])
    )

    },{validator:PasswordValidation.MatchPassword});
  }

  confirmPass():ValidatorFn{
    return (control: AbstractControl): {[key: string]: any} => {
      return {'confirm_pass': false};
    };
  }

  signUp(){
    this.navCtrl.push(CategoryPage);
  }

  login() {
    this.navCtrl.setRoot(HomePage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
    this.dataService.getCountry().then((data)=>{
      this.countries = data;
    });
  }

}



