import { DataServiceProvider } from './../../providers/data-service/data-service';
import { regexValidators } from './../validator';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import {FormControl, FormGroup, Validators} from '@angular/forms';

//@IonicPage()
@Component({
  selector: 'page-address-modal',
  templateUrl: 'address-modal.html',
})
export class AddressModalPage {

  countries:any;
  userInfo:any;

  constructor(public dataService:DataServiceProvider, public viewCtrl: ViewController, public navCtrl: NavController, public navParams: NavParams) {
    this.userInfo = new FormGroup({
      firstName: new FormControl('Himanshu',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.name)
      ])
    ),
      email: new FormControl('abc@gmail.com',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.email)
      ])
    ),
      mobile: new FormControl('1234567890',Validators.compose([
        Validators.required,
        Validators.maxLength(15),
        Validators.minLength(10),
        Validators.pattern(regexValidators.phone)
      ])
    ),
    lastName: new FormControl('Sharma',Validators.compose([
      Validators.required,
      Validators.pattern(regexValidators.name)
    ])
  ),
  country: new FormControl('United States',Validators.required),
  address: new FormControl('123, Food Street, Mathura',Validators.compose([
    Validators.required,
    Validators.pattern(regexValidators.address),
    Validators.minLength(20),
    Validators.maxLength(150)
  ])
),
city: new FormControl('New York',Validators.compose([
  Validators.required,
  Validators.pattern(regexValidators.location),
  Validators.minLength(3),
  Validators.maxLength(20)
])
),
      postCode: new FormControl('123456',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.postalCode)
      ])
    )
    });
  }

  saveAddr(){
    let data = {
      firstName:this.userInfo.value.firstName,
      lastName:this.userInfo.value.lastName,
      country:this.userInfo.value.country,
      address:this.userInfo.value.address,
      city:this.userInfo.value.city,
      postCode:this.userInfo.value.postCode
    };
   this.viewCtrl.dismiss(data);
  }

  close(){
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddressModalPage');
    this.dataService.getCountry().then((data)=>{
      this.countries = data;
    });
  }

}



