import { DataServiceProvider } from './../../providers/data-service/data-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';

//@IonicPage()
@Component({
  selector: 'page-filter-modal',
  templateUrl: 'filter-modal.html',
})
export class FilterModalPage {

  filterCost:number=0;
  prodCode:any=[];
  checkedProd:any=[];

  constructor(public dataService:DataServiceProvider, public viewCtrl: ViewController, public navCtrl: NavController, public navParams: NavParams) {
    dataService.getProducts().then((data)=>{
      console.log(data);
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          const element = data[key];
          if(this.prodCode.indexOf(element['productCode'])===-1){
            this.prodCode.push(element['productCode']);
          }
        }
      }

    });
  }

  datachanged(e:any){
    console.log(e);
    if(this.checkedProd.indexOf(e._item._label.text) === -1){this.checkedProd.push(e._item._label.text)}
    else{
      let index = this.checkedProd.indexOf(e._item._label.text);
      this.checkedProd.splice(index,1);
    }
    console.log(this.checkedProd);
}

  dismiss() {
   let data = { 'filterCost': this.filterCost, 'filterBrand':this.checkedProd};
   this.viewCtrl.dismiss(data);
 }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FilterModalPage');
  }

}



