import { MyApp } from './../../app/app.component';
import { CartPage } from './../cart/cart';
import { DataServiceProvider } from './../../providers/data-service/data-service';
import { ProductListPage } from './../product-list/product-list';
import { Component} from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { MenuController } from 'ionic-angular';

//@IonicPage()
@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
})
export class CategoryPage {

  loggedInUser:any;

  constructor(public toastCtrl:ToastController, public dataService:DataServiceProvider, public menuCtrl: MenuController, public navCtrl: NavController, public navParams: NavParams) {
    this.menuCtrl.enable(true);

    if(this.dataService.loggedInUser === undefined){
      this.loggedInUser = {"email":"sales@opensourcetechnologies.com","username":"OpenSource","password":"ost@2018"}
    }else{
      this.loggedInUser = this.dataService.loggedInUser;
    }
    MyApp.loggedInUser = this.loggedInUser;
  }

  badge(){
    return ProductListPage.notifier;
  }
  show(){
    return ProductListPage.show;
  }

  cart(){
    if(this.dataService.cartCost!=0){
      this.navCtrl.push(CartPage);
    }
    else{
      let toast = this.toastCtrl.create({
        message: 'Δεν υπάρχουν προϊόντα στο καλάθι',
        duration: 1500
      });
      toast.present();
    }
  }

  product(){
    this.navCtrl.push(ProductListPage);
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad CategoryPage');
  }

}



