import { MyApp } from './../../app/app.component';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, ValidatorFn, AbstractControl, FormGroup, FormControl } from '@angular/forms';
import { PasswordValidation } from '../signup/password-validation';
import { regexValidators } from './../validator';

//@IonicPage()
@Component({
  selector: 'page-change-settings-modal',
  templateUrl: 'change-settings-modal.html',
})
export class ChangeSettingsModalPage {

  passwordForm:FormGroup;

  constructor(public alertCtrl: AlertController, private formBuilder: FormBuilder, public viewCtrl:ViewController, public navCtrl: NavController, public navParams: NavParams) {

    this.passwordForm = this.formBuilder.group({
      oldPassword:new FormControl('',Validators.compose([
        Validators.pattern(regexValidators.password),
        Validators.required
      ])
    ),
      password: new FormControl('',Validators.compose([
        Validators.required,
        Validators.pattern(regexValidators.password)
      ])
    ),
      confirmPass: new FormControl('',Validators.compose([
        this.confirmPass,
        Validators.required,
        Validators.pattern(regexValidators.password)
      ])
    )

    },{validator:PasswordValidation.MatchPassword});
  }

  confirmPass():ValidatorFn{
    return (control: AbstractControl): {[key: string]: any} => {
      return {'confirm_pass': false};
    };
  }

  change(){
      if(MyApp.loggedInUser.password === this.passwordForm.value.oldPassword){
        MyApp.loggedInUser.password = this.passwordForm.value.password;
        let alert = this.alertCtrl.create({
          title: 'Change Settings',
          subTitle: 'Password has been successfully changed.',
          buttons: ['Dismiss']
        });
        alert.present();
      }else if(MyApp.loggedInUser.password !== this.passwordForm.value.oldPassword){
        let alert = this.alertCtrl.create({
          title: 'Change Settings',
          subTitle: 'Old password is incorrect',
          buttons: ['Dismiss']
        });
        alert.present();
      }
    this.navCtrl.pop();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ChangeSettingsModalPage');
  }

}



