import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams,ViewController } from 'ionic-angular';

declare var google:any;

//@IonicPage()
@Component({
  selector: 'page-location-modal',
  templateUrl: 'location-modal.html',
})
export class LocationModalPage{

  autocompleteItems: any;
  autocomplete = {
    query: ''
    };
  input:any;
  acService:any;

  constructor(public ref:ChangeDetectorRef, public viewCtrl:ViewController, public navCtrl: NavController, public navParams: NavParams) {
  }

  updateSearch() {

    if (this.autocomplete.query == '') {
    this.autocompleteItems = [];
    return;
    }
    let self = this;
    let config = {

    input: this.autocomplete.query,
    componentRestrictions: {  }
    }
    this.acService.getPlacePredictions(config, function (predictions, status) {

    self.autocompleteItems = [];
    if(predictions!== null){
      predictions.forEach(function (prediction) {
        self.autocompleteItems.push(prediction);
        self.ref.detectChanges();
        });
    }
    });
  }

  getLocation(pos){
    console.log(pos);
    this.viewCtrl.dismiss(pos);
  }

  ngOnInit() {
    this.acService = new google.maps.places.AutocompleteService();
    this.autocompleteItems = [];
    this.autocomplete = {
      query: ''
    };
  }

}



