import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter',
})
export class FilterPipe implements PipeTransform {

  filt : any=[];

  transform(array: Array<[{productName:string,"thumbnail":string,"productCode":string,"weight":number,"price":number}]>, args?: any): Array<[{productName:string,"thumbnail":string,"productCode":string,"weight":number,"price":number}]> {  
    console.log(args);
    if(typeof(args)!='undefined' && typeof(args.filterCost)!='undefined' && typeof(args.filterBrand)!='undefined'){
      if(args.filterCost !== 0 && args.filterBrand.length !== 0){
        console.log('both not zero');
        this.filt = [];
        args.filterBrand.forEach(element => {
          this.filt.push(array.filter(function( obj ) {
            return obj['productCode'] == element;
          }));
        });
         array = [].concat.apply([], this.filt);
        return array.filter((i)=>{
          if(args.filterCost !==0){
            return (i['price']<=args.filterCost);
          }
        });
      }else if(args.filterCost !== 0){  //(if filtercost is there)
        console.log('filter cost');
        if(args.filterBrand.length == 0){
          return array.filter((i)=>{
            if(args.filterCost !==0){
              return (i['price']<=args.filterCost);
            }
          }); 
        }
      }else if(args.filterCost == 0){  //(if filter brand is there)
        if(args.filterBrand.length !== 0){console.log('have brands');
          this.filt = [];
          args.filterBrand.forEach(element => {
            this.filt.push(array.filter(function( obj ) {
              return obj['productCode'] == element;
            }));
          });
          array = [].concat.apply([], this.filt);
          return array;
        }
      }else {return array}
    }
    return array;
  }

}



