import { AddressModalPage } from './../address-modal/address-modal';
import { DataServiceProvider } from './../../providers/data-service/data-service';
import { Component,ViewChild } from '@angular/core';
import { NavController, ModalController, NavParams, AlertController } from 'ionic-angular';
import { ToastController } from 'ionic-angular';
import { Slides } from 'ionic-angular';
import { CategoryPage } from '../category/category';
import { FindingPage } from '../finding/finding';
import { ProductListPage } from '../product-list/product-list';
import { Storage } from '@ionic/storage';

@Component({
  selector: 'page-checkout',
  templateUrl: 'checkout.html',
})
export class CheckoutPage {

  checkout={
    'product':null,
    'address':null,
    'shipMethod':'',
    'date':new Date().toISOString(),
    'time':new Date().toISOString(),
    'payMethod':''
  };
  cartDetail:{
    'subTotal':number,
    'shipMethod':string,
    'extra':number,
    'gross':number
  }={
    subTotal:0,
    shipMethod:'extend',
    extra:0,
    gross:0
  }

  addr:any;
  addrbtn:boolean;
  item:number;



  @ViewChild(Slides) slides: Slides;

  constructor(public modalCtrl: ModalController, private toastCtrl: ToastController, public dataService:DataServiceProvider, public alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams, public storage: Storage) {
    this.item = this.dataService.item;
    this.addrbtn = true;
    this.cartDetail.subTotal = this.navParams.get('cost');
    this.cartDetail.shipMethod = 'extend';
    this.cartDetail.gross = this.navParams.get('cost');
    this.cartDetail.extra = 0;
    this.dataService.checkoutAddr()
    .then((response)=> {
        this.addr = response;
    });
  }

  lockNextSlide(lock){
    let shouldLockSwipeToNext = lock;
    this.slides.lockSwipeToNext(shouldLockSwipeToNext);
  }

  next(){
    if(this.slides.getActiveIndex() == 0){
      if(this.checkout.address == null){
        this.lockNextSlide(true);
        let toast = this.toastCtrl.create({
          message: 'Παρακαλώ επιλέξτε τοποθεσία',
          duration: 3000,
          position: 'top'
        });
        toast.present();
      }else{
        this.checkout.address = this.dataService.userInfo.filter((i)=>{
          return i.address == this.checkout.address;
        });
        this.addrbtn = false;
        this.lockNextSlide(false);
        this.slides.slideNext(500, false);
        this.lockNextSlide(true);
      }
    }else if(this.slides.getActiveIndex() == 1){
      if(this.checkout.shipMethod == '' || this.checkout.date == '' || this.checkout.time == ''){
        this.lockNextSlide(true);
        let toast = this.toastCtrl.create({
          message: 'Δεν έχετε συμπληρώσει όλα τα στοιχεία',
          duration: 3000,
          position: 'top'
        });
        toast.present();
      }else{
        if(this.slides.getActiveIndex() == 1){
          if(this.checkout.shipMethod === 'extend'){
            this.cartDetail.shipMethod = 'Extend Free Shipping';
            this.cartDetail.gross = this.navParams.get('cost')+20;
            this.cartDetail.extra = 20;
          }else if(this.checkout.shipMethod === 'store'){
            this.cartDetail.shipMethod = 'Παραλαβή από το κατάστημα';
            this.cartDetail.gross = this.navParams.get('cost');
            this.cartDetail.extra = 0;
          }
        this.lockNextSlide(false);
        this.slides.slideNext(500, false);
        this.lockNextSlide(true);
      }
    }
    }
  }

  addAddr(){
    let addressModal = this.modalCtrl.create(AddressModalPage);
    addressModal.present();
    addressModal.onDidDismiss(data => {
      if(data !== undefined){
        this.dataService.addCheckoutAddr(data);
      }
    });
  }

  placeOrder(){
    if(this.checkout.payMethod !== ''){
      let alert = this.alertCtrl.create({
        title: 'Κατάσταση Παραγγελία;',
        subTitle: 'Η παραγγελία σας τοποθετήθηκε επιτυχώς. Σύντομα ένας υπεύθυνος θα επικοινωνήσει μαζί σας.',
        buttons: [
          {
            text: 'OK',
            handler: () => {
              this.checkout.product = this.dataService.cartProduct;
              this.dataService.cartProduct =[];
              this.dataService.item=0;
              ProductListPage.notifier = 0;
              ProductListPage.show = false;
              this.dataService.cartCost = 0;
              this.navCtrl.push(CategoryPage);
              this.checkout['gross amount'] = this.cartDetail.gross;
              console.log(this.checkout);
            }
          }]
      });
      alert.present();
    }else{
      let toast = this.toastCtrl.create({
        message: 'Παρακαλώ επιλέξτε τρόπο πληρωμής.',
        duration: 3000,
        position: 'top'
      });
      toast.present();
    }

  }

  finding(){
    this.navCtrl.push(FindingPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CheckoutPage');
    this.lockNextSlide(true);
  }

}



