import { ProductListPage } from './../product-list/product-list';
import { DataServiceProvider } from './../../providers/data-service/data-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ToastController } from 'ionic-angular';
import { CheckoutPage } from '../checkout/checkout';

//@IonicPage()
@Component({
  selector: 'page-cart',
  templateUrl: 'cart.html',
})
export class CartPage {

  static cost:number=0;
  cartProduct:any;
  products:any;
  static item:number=0;
  static dataservice:any;
  static navctrl:any;
  // plp:any=this;

  constructor(public toastCtrl: ToastController, public dataService:DataServiceProvider, public navCtrl: NavController, public navParams: NavParams) {
    CartPage.dataservice = dataService;
    CartPage.navctrl = navCtrl;
    CartPage.item = this.dataService.item;
    this.cartProduct = this.dataService.getCartProduct();
    CartPage.cost = this.dataService.cartCost;
    this.dataService.getProducts().then((data)=>{
      this.products = data;
    });
  }

  getCartCost(){
    return CartPage.cost;
  }
  getCartItem(){
    return CartPage.item;
  }

  removeProduct(product){
    product.inCart ="Add";
    product.disabled = false;
    CartPage.item--;
    --ProductListPage.notifier;
    if(ProductListPage.notifier === 0){
      ProductListPage.show = false;
    }console.log(CartPage.dataservice);
    CartPage.cost = CartPage.dataservice.removeCartProduct(product,CartPage.cost);
    // let toast = this.toastCtrl.create({
    //   message: product.productName+' removed from cart successfully',
    //   duration: 1500
    // });
    // toast.present();
    if(CartPage.cost===0){
      CartPage.navctrl.pop();
    }
  }

  itemIncrease(product){
    this.cartProduct.forEach(function(pro) {
      if (product.productName === pro.productName) {
        if(pro.item !== 5){
          pro.item++;
          CartPage.cost += pro.price;
        }
      }
    });
  }

  itemDecrease(product){
    this.cartProduct.forEach(function(pro) {
      if (product.productName === pro.productName) {
        if(pro.item !== 1){
          pro.item--;
          CartPage.cost -= pro.price;
          console.log(CartPage.cost);
        }else if(pro.item === 1){
          console.log(CartPage.prototype.removeProduct(pro));
        }
      }
    });
  }

  checkout(){
    this.navCtrl.push(CheckoutPage,{cost:CartPage.cost});
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CartPage');
  }

}



