import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'sort',
})
export class SortPipe implements PipeTransform {

  transform(array: Array<[{productName:string,"thumbnail":string,"productCode":string,"weight":number,"price":number}]>, args?: any): Array<[{productName:string,"thumbnail":string,"productCode":string,"weight":number,"price":number}]> { 
    array.sort((a: any, b: any) => {
        if(args.property === 'htl'){
          if ( a.price < b.price ){
            return 1;
          }
        }else if(args.property === 'lth'){
          if ( a.price > b.price ){
            return 1;
          }
        }else if(args.property === 'atoz'){
          if ( a.productName > b.productName ){
            return 1;
          }
        }else if(args.property === 'ztoa'){
          if ( a.productName < b.productName ){
            return 1;
          }
        }
      });
      return array;
  }
}



