import { NotificationPage } from './../pages/notification/notification';
import { SettingsPage } from './../pages/settings/settings';
import { ProductListPage } from './../pages/product-list/product-list';
import { DataServiceProvider } from './../providers/data-service/data-service';
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, MenuController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  showLevel1 = null;
  showLevel2 = null;

  static loggedInUser:any='';

  rootPage: any = HomePage;

  pages: any;

  constructor(public menuCtrl: MenuController, public dataService: DataServiceProvider, public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen) {
    this.initializeApp();
    this.menuCtrl.close();
    this.dataService.getMenus()
    .then((response)=> {
        this.pages = response;
    });
  }

  /*platform.ready().then(() => {
      // Here we will check if the user is already logged in
      // because we don't want to ask users to log in each time they open the app
      let env = this;
      this.nativeStorage.getItem('user')
      .then( function (data) {
        // user is previously logged and we have his data
        // we will let him access the app
        env.nav.push(UserPage);
        env.splashScreen.hide();
      }, function (error) {
        //we don't have the user data so we will ask him to log in
        env.nav.push(LoginPage);
        env.splashScreen.hide();
      });

      this.statusBar.styleDefault();
    });*/

  notification(){
    this.menuCtrl.close();
    this.nav.push(NotificationPage);
  }

  settings(){
    this.menuCtrl.close();
    this.nav.push(SettingsPage);
  }

  logOut(){
    this.nav.setRoot(HomePage);
  }

  getLoggedInUser(){
    return MyApp.loggedInUser.username;
  }
  initializeApp() {
    this.platform.ready().then(() => {

      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  toggleLevel1(idx) {
    if (this.isLevel1Shown(idx)) {
      this.showLevel1 = null;
    } else {
      this.showLevel1 = idx;
    }
  };

  toggleLevel2(idx) {
    if (this.isLevel2Shown(idx)) {
      this.showLevel1 = null;
      this.showLevel2 = null;
    } else {
      this.showLevel1 = idx;
      this.showLevel2 = idx;
    }
  };
  isLevel1Shown(idx) {
    return this.showLevel1 === idx;
  };

  isLevel2Shown(idx) {
    return this.showLevel2 === idx;
  };

  openPage() {

    this.menuCtrl.close();
    this.nav.push(ProductListPage);
  }
}



