import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import {Validators, FormBuilder, FormGroup } from '@angular/forms';
import { EmailComposer } from '@ionic-native/email-composer';

//@IonicPage()
@Component({
  selector: 'page-feedback',
  templateUrl: 'feedback.html',
})
export class FeedbackPage {
  subject="";

  constructor(private emailComposer: EmailComposer, public alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams) {
  }

  /*this.emailComposer.isAvailable().then((available: boolean) =>{
    if(available) {
   //Now we know we can send
  }
});*/

  sendFeed(){
    let email ={
      to: "info@fastdeliveryhellas.gr",
      cc: "gmanias90@gmail.com",
      subject: this.subject,
      isHtml: true
    }

    this.emailComposer.open(email);

    let alert = this.alertCtrl.create({
      title: 'Σχόλια',
      subTitle: 'Σας ευχαριστούμε για τα σχόλια σας. Προσπαθούμε πάντα να γινόμαστε καλύτεροι.',
      buttons: [
        {
          text:'OK',
          handler: data => {
            this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
}

  ionViewDidLoad() {
    console.log('ionViewDidLoad FeedbackPage');
  }

}



