import { ProductDetailPage } from './../product-detail/product-detail';
import { DataServiceProvider } from './../../providers/data-service/data-service';
import { Component,ViewChild,ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CartPage } from '../cart/cart';
import { ToastController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { FilterModalPage } from '../filter-modal/filter-modal';
import { ModalController} from 'ionic-angular';
import { Vibration } from '@ionic-native/vibration';

//@IonicPage()
@Component({
  selector: 'page-product-list',
  templateUrl: 'product-list.html',
})
export class ProductListPage {

  static notifier:number=0;
  static show:boolean=false;
  terms:string='';
  sortName: string = 'default';
  products:any;
  filterData:any;
  item:number=1;
  cartProduct:any;
  modifiedCost:any;

  @ViewChild ('weightSelect') weightSelect : ElementRef;

  constructor(private vibration: Vibration, public modalCtrl: ModalController, public alertCtrl: AlertController, public toastCtrl: ToastController, public dataService:DataServiceProvider, public navCtrl: NavController, public navParams: NavParams) {
    this.cartProduct = this.dataService.getCartProduct();
    this.dataService.getProducts()
    .then((response)=> {
        this.products = response;
        console.log(response);
    });
  }

  selectCost(p,e:any){
    p.unit.forEach(element => {
      if(element.weight === e){
        p.defaultPrice = element.price;
        p.weight = element.weight;
      }
    });
  }

  detail(p){
    let detailmodal = this.modalCtrl.create(ProductDetailPage,{data:p});
    detailmodal.present();
    detailmodal.onDidDismiss(data => {
      if(data){
        this.addProduct(p);
      }
    });
  }

  cartItem(){
    if(this.products !== undefined){
    this.products.filter((object)=>{
      this.cartProduct.forEach(element => {
        if(element === object){
          object['item'] = element['item'];
        }
      });
    });
  }
  }

  itemIncrease(product){
    this.dataService.cartProduct.forEach(element => {
      if(element == product){
        if(product.item < 5){
          product.item++;
          this.dataService.cartCost = product.price*product.item;
        }
      }
    });
  }

  itemDecrease(product){
    this.dataService.cartProduct.forEach(element => {
      if(element == product){
        if(product.item !== 1){
          product.item--;
          this.dataService.cartCost = product.price*product.item;
        }else if(product.item === 1){
          product.inCart = "Προσθήκη";
          product.disabled = false;
          this.dataService.removeCartProduct(product,product.price);
          ProductListPage.notifier--;
          if(ProductListPage.notifier === 0){
            ProductListPage.show = false;
          }
        }
      }
    });
  }

  addOrNot(p){
    if(p.inCart === "Προσθήκη"){
      p.inCart = "Προστέθηκε";
      p.disabled = true;
    }
  }

  getItem(){
    return CartPage.item;
  }

  notification(){
    return ProductListPage.notifier;
  }
  show(){
    return ProductListPage.show;
  }

  addProduct(product){
    let weight = this.weightSelect["_text"]
    product.unit.filter((data)=>{
      if(data.weight === weight){
        product.price = data.price;
        product.weight = data.weight;
      }
    });
    this.addOrNot(product);
    let item = this.dataService.cartProduct.find(p=>p==product);
    if(item === undefined || ProductListPage.notifier === undefined){
      // product.item=1;
      this.dataService.addToCart(product);
      ++ProductListPage.notifier;
      ProductListPage.show=true;
      this.show();
      this.cartItem();
      this.vibration.vibrate([200,100,300]);
      let toast = this.toastCtrl.create({
        message: product.productName+' προστέθηκε επιτυχώς στο καλάθι σας',
        duration: 1500
      });
      toast.present();
    }else{
      let toast = this.toastCtrl.create({
        message: product.productName+' έχει προστεθεί ήδη στο καλάθι σας',
        duration: 1500
      });
      toast.present();
    }
  }

  cart(){
    if(this.dataService.cartCost!=0){
      this.navCtrl.push(CartPage);
    }
    else{
      let toast = this.toastCtrl.create({
        message: 'Δεν υπάρχουν προϊόντα στο καλάθι σας',
        duration: 1500
      });
      toast.present();
    }
  }

  sort(){
    let alert = this.alertCtrl.create();
    alert.setTitle('Ταξινόμηση Προϊόντων');

    alert.addInput({
      type: 'radio',
      label: 'Με βάση τον τίτλο Α-Ω',
      value: 'atoz',
      checked: false
    });
    alert.addInput({
      type: 'radio',
      label: 'Με βάση τον τίτλο Ω-Α',
      value: 'ztoa',
      checked: false
    });
    alert.addInput({
      type: 'radio',
      label: 'Με βάση την τιμή- Χαμηλότερη προς Υψηλότερη',
      value: 'lth',
      checked: false
    });
    alert.addInput({
      type: 'radio',
      label: 'Με βάση την τιμή- Υψηλότερη προς Χαμηλότερη',
      value: 'htl',
      checked: false
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'OK',
      handler: data => {
        this.sortName=data;
      }
    });
    alert.present();
  }

  filter(){
    let filtermodal = this.modalCtrl.create(FilterModalPage);
    filtermodal.present();
    filtermodal.onDidDismiss(data => {
      this.filterData = data;

    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductListPage');
  }
  ionViewWillEnter(){
    this.cartItem();
  }

}



