import { LocationModalPage } from './../location-modal/location-modal';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { NativeGeocoder, NativeGeocoderReverseResult } from '@ionic-native/native-geocoder';
import { CategoryPage } from '../category/category';

//@IonicPage()
@Component({
  selector: 'page-location',
  templateUrl: 'location.html',
})
export class LocationPage {

  position:any;
  location:any='';

  constructor(public modalCtrl:ModalController, private nativeGeocoder: NativeGeocoder, private geolocation: Geolocation, public navCtrl: NavController, public navParams: NavParams) {

  }

  locate(){
    this.geolocation.getCurrentPosition().then((resp) => {
      this.nativeGeocoder.reverseGeocode(resp.coords.latitude, resp.coords.longitude)
      .then((result: NativeGeocoderReverseResult[]) => {
        // console.log(JSON.stringify(result));
        this.location =  JSON.stringify(result);
        this.navCtrl.push(CategoryPage);
      })
      .catch((error: any) => {console.log(error);this.navCtrl.push(CategoryPage);});

     }).catch((error) => {
       console.log('Error getting location', error);
       this.navCtrl.push(CategoryPage);
     });
  }

  locateManually(){
    let locationModal = this.modalCtrl.create(LocationModalPage);
    locationModal.present();
    locationModal.onDidDismiss(data => {
      this.location = data;
      console.log('User Location : ',this.location);
      if(this.location !== undefined){
        setTimeout(() => {
          this.navCtrl.push(CategoryPage);
        }, 2000);
      }
    });
  }

  skip(){
    this.navCtrl.push(CategoryPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LocationPage');
  }

}



