import { AboutPage } from './../about/about';
import { ChangeSettingsModalPage } from './../change-settings-modal/change-settings-modal';
import { MyApp } from './../../app/app.component';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ModalController  } from 'ionic-angular';
import { FeedbackPage } from '../feedback/feedback';
import { ProfilePage } from '../profile/profile';

//@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {

  username:string;
  email:string;
  password:string;

  constructor(public modalCtrl: ModalController, public alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams) {
    this.username = MyApp.loggedInUser.username;
    this.email = MyApp.loggedInUser.email;
    this.password = MyApp.loggedInUser.password;
    console.log(this.username,this.email,this.password);
  }

  change(){
    let changeModal = this.modalCtrl.create(ChangeSettingsModalPage);
    changeModal.present();

  }

  feed(){
    this.navCtrl.push(FeedbackPage);
  }
  profile(){
    this.navCtrl.push(ProfilePage);
  }
  about(){
    this.navCtrl.push(AboutPage);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SettingsPage');
  }

}



