import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';


@Injectable()
export class DataServiceProvider {

  countryurl = "assets/data/countries.json";
  cartCost:number=0.00;
  apiUrl="assets/data/menu.json";
  productsData = "assets/data/products.json";
  user = "assets/data/userDetail.json";
  userInfo:any=[];
  cartProduct:Array<[{productName:string,"thumbnail":string,"productCode":string,"weight":number,"price":number,"item":number}]>=[];
  userCredentials:any;
  loggedInUser:any;
  item:number=0;
  wishlist:any=[];

  constructor(public http: HttpClient) {
    this.getUser().then((data)=>{

      this.userCredentials = data;
    });
    console.log('Hello DataServiceProvider Provider');
  }

  getCountry(){
    return new Promise(resolve => {
      this.http.get(this.countryurl).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  getUser(){
    return new Promise(resolve => {
      this.http.get("assets/data/users.json").subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  validateUser(email,pass){
    let flag:boolean = false;
    this.userCredentials.forEach(element => {
      if(element.email === email && element.password === pass){
        flag = true;
        this.loggedInUser = {"username":element.username,"email":element.email,"password":element.password}
      }
    });
    return flag;
  }

  getMenus(){
    return new Promise(resolve => {
      this.http.get(this.apiUrl).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });

  }

  getProducts(){
    return new Promise(resolve => {
      this.http.get(this.productsData).subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });

  }

  addToCart(product){
    this.item++;
    this.cartProduct.push(product);
    this.cartCost += product.price*product.item;
  }

  checkoutAddr(){
    return new Promise(resolve => {
      this.http.get(this.user).subscribe(data => {
        this.userInfo = data;
        resolve(this.userInfo);
      }, err => {
        console.log(err);
      });
    });
  }

  addCheckoutAddr(data){
   this.userInfo.push(data);
   console.log(this.userInfo);
  }

  getCartProduct(){
    return this.cartProduct;
  }

  removeCartProduct(product,cost){
    this.item--;
    this.cartCost = cost;
    let index = this.cartProduct.indexOf(product);
    this.cartProduct.splice(index,1);
    return this.cartCost  = Number((this.cartCost - (product.price*product.item)).toFixed(2));
  }

}



